.product img{
    width: 286px;
    height: 286px;
    margin: 7px;
}
.product{
    width:300px;
    height: 510px;
    border: 1px solid #95A0A7;
    border-radius: 8px;
    position: relative;
   
}
.product-name{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 21px;
    margin-top: 7px;
}
.product-info{
    margin: 0px 14px;
}
.product-info p{
    margin-bottom: 0px;
    margin-top: 5px;
}
.btn-cart{
    width: 100%;
    height: 48px;
    border: 0px;
    background-color: #FFE0B3;
    border-radius: 0px 0px 8px 8px;
    position: absolute;
    bottom: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.btn-cart:hover{
    background-color: orange;
}

.btn-text{
    margin-right: 5px;
}